<template>
  <div>
    <b-card-group columns class="cardGroup">
      <b-card v-for="project in projects" :key="project.id"
              :title="project.title"
              :img-src="project.src"
              :img-alt="project.alt"
              img-top
              class="img-card"
              @click="goTo(project.goToPage)">
        <p class="card-text">
          {{ project.period }}
        </p>
      </b-card>
    </b-card-group>
    <router-view></router-view>
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'

  const {mapState} = createNamespacedHelpers('work');

  export default {
    name: "Work",
    data() {
      return {
        color: '#fff',
        size: '20px',
        loading: ''
      }
    },
    created() {
      this.passTheStatus('work');
    },
    mounted() {
      this.$store.dispatch('work/getProjects');
    },
    methods: {
      goTo(value) {
        this.$router.push(value);
      }
    },
    computed: {
      ...mapState(['projects'])
    }
  }
</script>

